.side-menu-link {
    display: block;
    text-align: left;
    width: 100%;
    height: 48px;
    padding: 0px 16px;
    margin: 0px;
    text-transform: none;
    border-radius: 0 !important;
    white-space: normal;
    line-height: 48px;
    position: relative;
    z-index: 2;
    box-shadow: none !important;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.side-menu-link:hover, .side-menu-link:focus {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.uppercase-all .side-menu-link {
    text-transform: uppercase;
}

.uppercase-top-level .side-menu-link {
    text-transform: uppercase;
}

.uppercase-top-level .drop-down-list .side-menu-link {
    text-transform: none;
