.timeline {
    position: relative;
    overflow: hidden;
    padding: 10px 0;
}

.timeline .timeline-widget {
    z-index: 4;
    opacity: 0;
    overflow: hidden;
}

.timeline .timeline-point {
    position: absolute;
    top: 50%;
    right: 50%;
    width: 100px;
    height: 100px;
    -webkit-transform: translate3D(50%, -50%, 0);
    -ms-transform: translate3D(50%, -50%, 0);
    transform: translate3D(50%, -50%, 0);
    border-radius: 50%;
    z-index: 2;
    overflow: hidden;
}

.timeline .timeline-point-date {
    position: absolute;
    z-index: 3;
    background: inherit;
    top: 50%;
    left: 50%;
    width: 0;
    height: 0;
    line-height: 100px;
    overflow: hidden;
    transition: width .5s, height 0.5s;
    -webkit-transform: translate3d(-50%, -50%, 0);
    transform: translate3d(-50%, -50%, 0);
    text-align: center;
    font-size: 0.8em;
    border-radius: 50%;
}

.timeline:hover .timeline-point-date {
    width: 100%;
    height: 100%;
}

.timeline .timeline-y-axis {
    margin: 0;
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    -webkit-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    transform: rotate(90deg);
    z-index: 1;
}

.timeline:first-child .timeline-y-axis {
    -webkit-transform: rotate(90deg) translateX(50%);
    -ms-transform: rotate(90deg) translateX(50%);
    transform: rotate(90deg) translateX(50%);
}

.timeline:last-child .timeline-y-axis {
    -webkit-transform: rotate(90deg) translateX(-50%);
    -ms-transform: rotate(90deg) translateX(-50%);
    transform: rotate(90deg) translateX(-50%);
}

.timeline .timeline-x-axis {
    opacity: 0;
}

.timeline-content-animated {
    opacity: 1 !important;
}

.timeline-content-animated.left {
    -webkit-animation-name: slideInLeft;
    -ms-animation-name: slideInLeft;
    animation-name: slideInLeft;
    -webkit-animation-iteration-count: 1s;
    -ms-animation-iteration-count: 1s;
    animation-iteration-count: 1s;
    -webkit-animation-duration: 1s;
    -ms-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-delay: 0s;
    -ms-animation-delay: 0s;
    animation-delay: 0s;
    -webkit-animation-timing-function: ease;
    -ms-animation-timing-function: ease;
    animation-timing-function: ease;
    -webkit-animation-fill-mode: both;
    -ms-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-backface-visibility: hidden;
    -ms-backface-visibility: hidden;
    -o-backface-visibility: hidden;
    backface-visibility: hidden;
}

.timeline-content-animated.right {
    -webkit-animation-name: slideInRight;
    -ms-animation-name: slideInRight;
    animation-name: slideInRight;
    -webkit-animation-iteration-count: 1s;
    -ms-animation-iteration-count: 1s;
    animation-iteration-count: 1s;
    -webkit-animation-duration: 1s;
    -ms-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-delay: 0s;
    -ms-animation-delay: 0s;
    animation-delay: 0s;
    -webkit-animation-timing-function: ease;
